import React, { useState } from 'react';
import words from '../data/words.json'; // JSONファイルを読み込み
import { Button } from '@mui/material';

const WordTyping: React.FC = () => {
  const [currentIndex, setCurrentIndex] = useState(0); // 現在の単語のインデックス
  const [userInput, setUserInput] = useState(''); // ユーザーの入力
  const [isCorrect, setIsCorrect] = useState(false); // 正解判定

  const currentWord = words[currentIndex];

  // 入力が変わったときの処理
  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const value = e.target.value;
    setUserInput(value);

    // 入力が正しいかチェック
    if (value === currentWord.english) {
      setIsCorrect(true);
    } else {
      setIsCorrect(false);
    }
  };

  // 次の単語に進む
  const handleNextWord = () => {
    setUserInput(''); // 入力をクリア
    setIsCorrect(false); // 正解状態をリセット
    setCurrentIndex((prevIndex) => (prevIndex + 1) % words.length); // 次の単語へ
  };

   // エンターキーの処理
  const handleKeyDown = (e: React.KeyboardEvent<HTMLInputElement>) => {
    if (e.key === 'Enter') {
      handleNextWord(); // エンターキーで次の単語に進む
    }
  };

  return (
    <div style={{ textAlign: 'center', color: 'white', marginTop: '20px' }}>
  <h2 style={{ fontSize: '48px', margin: '20px 0' }}>{currentWord.japanese}</h2>
  {/* 英単語表示 */}
  <p style={{ fontSize: '40px', margin: '10px 0', color: 'lightgray' }}>
    {currentWord.english}
  </p>
  {/* 入力欄と次へボタン */}
  <div
  style={{
    display: 'flex',
    position: 'relative', // 入力エリアを固定するための相対位置
    margin: '20px auto', // ページ中央揃え（上下20pxの余白）
    width: '500px', // 入力エリアを固定サイズにする
  }}
>
  {/* 入力エリア */}
  <input
    type="text"
    value={userInput}
    onChange={handleInputChange}
    onKeyDown={handleKeyDown}
    style={{
      padding: '20px',
      fontSize: '40px',
      width: '100%', // 親の幅に依存
      textAlign: 'center',
      color: isCorrect ? 'lightgreen' : 'white',
      backgroundColor: '#1e1e2f',
      border: isCorrect ? '2px solid lightgreen' : '2px solid gray',
      borderRadius: '10px',
    }}
  />
  {/* エンターキー画像 */}
  {isCorrect && (
    <img
      src="/images/enterkey.png"
      alt="次に進む"
      onClick={handleNextWord}
      style={{
        position: 'absolute', // 入力エリアに影響を与えないように絶対位置を使用
        top: '50%', // 入力エリアの中央揃え
        left: '510px', // 入力エリアの右横に配置（入力欄の幅200px + マージン）
        transform: 'translateY(-50%)', // 縦方向の中央揃え調整
        width: '80px',
        height: '80px',
        cursor: 'pointer',
      }}
    />
  )}
</div>
</div>
  );
};

export default WordTyping;
